import { workTypeDict, workStatusDict } from '@/utils/dict'
import photographImg from '@/assets/staff/home/complateConfirm/photograph.png'

export const renderFormConfig = {
	[workTypeDict.pick]: [
		{
			label: '上传图片',
			placeholder: '支持上传多张图片',
			type: 'upload',
			key: 'uploadFile',
			overlay: photographImg,
			multiple: true,
			title: "立即拍照",
			capture: "environment",
			required: true
		},
		{
			inputType: 'BUTTON',
			type: 'input',
			label: '确认完成'
		}

	],
	[workTypeDict.productPackaging]: {
		[workStatusDict.waitExecute]: [
			{
				label: '生产批次',
				placeholder: '请选择生产批次',
				type: 'select',
				key: 'productBatchCode',
				required: true,
				options: []
			},
			{
				label: '扫描产品码',
				placeholder: '支持扫描批量产品码',
				type: 'scanCode',
				key: 'idisCodeList',
				required: true,
				multiple: true,
				isDedup: true
			},
			{
				label: '上传图片',
				placeholder: '支持上传多张图片',
				type: 'upload',
				key: 'uploadFile',
				overlay: photographImg,
				multiple: true,
				title: "立即拍照",
				capture: "environment",
				required: true
			}
		],
		[workStatusDict.execute]: [
			{
				label: '生产批次',
				placeholder: '请选择生产批次',
				type: 'select',
				key: 'productBatchCode',
				required: true,
				disabled: true,
				options: []
			},
			{
				label: '扫描产品码',
				placeholder: '支持扫描批量产品码',
				type: 'scanCode',
				key: 'idisCodeList',
				required: true,
				multiple: true,
				isDedup: true
			}
		]
	},
	[workTypeDict.secondaryPackaging]: [
		{
			label: '扫描外包装码',
			placeholder: '只支持扫描一个外包装码',
			type: 'scanCode',
			key: 'idisCode',
			required: true,
			remark: "",
			isDedup: true
		},
		{
			label: '扫描产品码',
			placeholder: '支持批量扫描产品码',
			type: 'scanCode',
			key: 'idisCodeList',
			required: true,
			multiple: true,
			isDedup: true
		},
	],
	[workTypeDict.qualityTesting]: [
		{
			label: '生产批次',
			placeholder: '请选择生产批次',
			type: 'select',
			key: 'productBatchCode',
			required: true,
			options: []
		},
		{
			label: '检验报告',
			type: 'upload',
			key: 'fileList',   //qualityReport
			required: true,
			multiple: true,
			uploadMode: 'button',
			accept: "image/*, .pdf",
			title: '支持上传图片或pdf,支持上传多份'
		},
		{
			inputType: 'BUTTON',
			type: 'input',
			label: '确认完成'
		}
	]

}


